.card {
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
}

.card,
.card-header,
.jumbotron {
    border: none;
    border-radius: 0;
}

.jumbotron {
    background-color: rgba(0, 0, 0, 0.05);
}

/* cabin-sketch-regular - latin */
@font-face {
  font-family: 'Cabin Sketch';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Cabin Sketch Regular'), local('CabinSketch-Regular'),
       url(/openhex/static/media/cabin-sketch-v11-latin-regular.518e5a78.woff2) format('woff2'), 
       url(/openhex/static/media/cabin-sketch-v11-latin-regular.40eb57a7.woff) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

/* neucha-regular - latin */
@font-face {
  font-family: 'Neucha';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Neucha'),
       url(/openhex/static/media/neucha-v9-latin-regular.0df438fe.woff2) format('woff2'), 
       url(/openhex/static/media/neucha-v9-latin-regular.d7c6d0e6.woff) format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}

html, body, #root, .App {
    height: 100%;
}
#grid {
    height: 100%;
}

.App {
    display: flex;
}
.card-menu, #grid {
    flex: auto;
}

#grid {
    background: rgb(59, 114, 218);
}

svg g .all-hexs {
    -webkit-filter: url(#world-shadow);
            filter: url(#world-shadow);
}

svg g .hex {
    fill: #3F51B5;
}
svg g .hex.color-0 {
    fill: #28a745; /* success */
}
svg g .hex.color-1 {
    fill: #c82333; /* danger */
}
svg g .hex.color-2 {
    fill: #e0a800; /* warning */
}
svg g .hex.color-3 {
    fill: #868e96; /* text-muted */
}
svg g .hex.color-4 {
    fill: #138496; /* info */
}
svg g .hex.color-5 {
    fill: #424242; /* secondary */
}

svg .hex-clickable {
    cursor: pointer;
}
.has-move .unit {
    -webkit-animation: bounce 1s infinite;
            animation: bounce 1s infinite;
    cursor: -webkit-grab;
    cursor: grab;
}
.has-selection,
.has-selection .has-move .unit,
.has-selection .hex-clickable {
    cursor: -webkit-grabbing;
    cursor: grabbing;
}

@-webkit-keyframes bounce {
 0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
 40% {-webkit-transform: translateY(-8px);transform: translateY(-8px);}
 60% {-webkit-transform: translateY(-4px);transform: translateY(-4px);}
}

@keyframes bounce {
 0%, 20%, 50%, 80%, 100% {-webkit-transform: translateY(0);transform: translateY(0);}
 40% {-webkit-transform: translateY(-8px);transform: translateY(-8px);}
 60% {-webkit-transform: translateY(-4px);transform: translateY(-4px);}
}

.inline-buttons {
    display: flex;
}
.inline-buttons button {
    flex: auto;
    margin: 0 0.6125rem;
}
.inline-buttons button:first-child {
    margin-left: 0;
}
.inline-buttons button:last-child {
    margin-right: 0;
}

.card-menu-small, .card-menu-large {
    position: fixed;
    z-index: 5000;
}
.card-menu-small-kingdom {
    top: 0;
    width: 100%;
}
.card-menu-small-game {
    bottom: 0;
    width: 100%;
}
@media (max-width: 767px) { /* md */
    .card-body {
        padding: 0.8em;
    }
    .inline-buttons button {
        margin: 0 0.4rem;
    }
}
.card-menu-large {
    right: 0;
    top: 0;
    width: 40%;
    height: 100%;
}
@media (min-width: 1200px) { /* xl */
    .card-menu-large {
        width: 28em;
    }
}
.card-menu-large .game-menu {
    position: absolute;
    bottom: 0;
    width: 100%;
}
.kingdom-menu .selection-container {
    height: 40px;
}
.money-display {
    font-size: 1.125em;
}
@media (min-width: 576px) { /* sm */
    .money-display {
        font-size: 1.5em;
    }
}

.selected-kingdom {
    -webkit-filter: url(#hex-shadow);
            filter: url(#hex-shadow);
}
.warning-entity {
    -webkit-filter: url(#warn-highlight);
            filter: url(#warn-highlight);
}
.capital-can-buy {
    -webkit-filter: url(#gold-highlight);
            filter: url(#gold-highlight);
}

.alerts-wrapper {
    width: 100%;
    position: fixed;
    z-index: 10000;
    margin-top: 6em;
}
@media (min-width: 768px) { /* md */
    .alerts-wrapper {
        width: 60%;
        margin-top: 1em;
    }
}
@media (min-width: 1200px) { /* xl */
    .alerts-wrapper {
        width: calc(100% - 28em);
    }
}
.alerts {
    width: 100%;
    max-width: 42em;
}
.alerts .alert {
    border: none;
}
.alerts .alert p {
    margin: 0;
}

.play-button {
    font-size: 2em;
}


.create-custom-game .btn-block {
    margin-bottom: 1em;
}
.create-custom-game .play-button {
    width: 100%;
    max-width: 20em;
    margin: auto;
    margin-top: 3em;
}
.create-custom-game h2,
.create-custom-game h3
{
    margin-top: 1em;
}
.create-custom-game .play-button {
    margin-bottom: 4em;
}

.home main {
    position: relative;
    z-index: 5;
}
.home .splashscreen-background-wrapper > div {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.home .splashscreen-background-wrapper .splashscreen-background-overlay {
    z-index: 3;
    background-color: rgba(255, 255, 255, 0.8);
}
.home .splashscreen-background-wrapper .splashscreen-background {
    z-index: 2;
    top: 1em;
    padding: 0 1em;
}
.home .main-menu .btn {
    margin-bottom: 0.5em;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
}

.learn-to-play.container {
    max-width: 32em;
    font-size: 1.25em;
    margin-bottom: 8em;
}
.learn-to-play h1,
.learn-to-play h2,
.learn-to-play h3
 {
    margin: 2em 0 0.75em 0;
}
.learn-to-play .space-after {
    margin-bottom: 0.5em;
}
.learn-to-play ul.maintenance-list li {
    margin-bottom: 0.5em;
}
.learn-to-play.tldr .hidden-on-tldr {
    display: none;
}
.learn-to-play.tldr .d-block.hidden-on-tldr {
    display: none !important;
}

.learn-to-play .card,
.learn-to-play .card-header {
    border: 2px solid #333;
    border-radius: 5px 5px 5px 5px/25px 25px 25px 5px;
    box-shadow: none;
}

